/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import transactionsData from './data-bb-transactions-http';

const transactionsDataModuleKey = 'data-bb-transactions-http-ng';

export const transactionsDataKey = 'data-bb-transactions-http-ng:transactionsData';

export default ng
  .module(transactionsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(transactionsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       transactionsData(config),
      ],
    };
  }])
  .name;



